import React, {Component} from 'react';
import './css/Body.css';
import profile from "./media/Profile_Pic.jpg"

class About extends React.Component {
  state = {
    isAbout: true
  }
  contentHandler = () => {
    if (this.state.isAbout) {
      this.setState({
        isAbout: false
      })
    } else {
      this.setState({
        isAbout: true
      })
    }
  }

  testMethod() {
    if(this.state.isAbout) {
      return (
        <div className="AboutInfo">
            <img className="Profile" src={profile} alt="Profile Photo"></img>
            <p className="AboutPara">Hi I am Arun Kumarr and I love to code.</p>
          </div>
      );
    } else {
      return (
        <div className="AboutPara">
          <p>I studied in NUS</p>
        </div>
      );
    }
  }
  render() {
    return (
      <div className="Card">
        <div className="Option">
          <button onClick={this.contentHandler}><i>About Me</i></button>
          <button onClick={this.contentHandler}><i>Education</i></button>
        </div>
        { this.testMethod() }
      </div>
    );
  }
}

class Body extends React.Component {
    render() {
        return (
            <div className="Body">
              <About />
            </div>
        );
    }
}

export default Body;